            v-model="skillComputer.other[0]"
          />
        </tr>
        <tr>
          <th scope="row">Tốt</th>
          <td><input type="radio" v-model="skillComputer.word" value="2" /></td>
          <td>
            <input type="radio" v-model="skillComputer.excel" value="2" />
